















import Vue from "vue";

export default Vue.extend({
  // category 선택시 store 사용하여 렌더링
  data() {
    return {
      selected: "ALL",
      list: [
        "ALL",
        "HTTP",
        "SpringBoot",
        "Node.js",
        "Vue.js",
        "React.js",
        "ect",
      ],
    };
  },
  methods: {
    listClick(tapName:string) {
      this.selected = tapName;
    },
  },
});
